* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
p,
div {
  margin: 0;
  padding: 0;
}

.country-list-item {
  display: flex;
  list-style: none;
  margin-bottom: 15px;
}

img {
  margin-right: 10px;
}

.country-list-name {
  font-size: 20px;
}

.country-name {
  font-size: 30px;
  font-weight: 600;
}

.country {
  display: flex;
  margin-bottom: 30px;
}

.country-info p span {
  font-weight: 700;
}
.country-info p {
  margin-bottom: 15px;
}
